/*Header*/
.header {
    background: rgb(41, 41, 41);
}

.header-title {
    font-size: 40px;
    padding-top: 20px;
    text-align: center;
    color: rgb(193, 252, 252);
}

.header-nav {
    display: flex;
    width: 17%;
    min-width: 255px;
    justify-content: space-between;
    margin-left: 20px;
    padding-bottom: 20px;
}

.header-link {
    font-size: 30px;
    text-decoration: none;
    color: white;
}
/*Homepage*/
.homepage,
.about,
.not-found-page-container {
    border: 1px solid rgb(233, 247, 248);
    height: 100vh;
    background: rgb(233, 247, 248);
}

.homepage-title,
.about-title {
    font-size: 36px;
    text-align: center;
    color: rgb(14, 89, 119);
    padding-top: 20px;
}
/*About us page*/
.about-content {
    padding-left: 20px;
    font-size: 24px;
}
/* Cards */
.cards-container {
    width: 100%;
    height: 88vh;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
}
.card {
    width: 250px;
    height: 250px;
    margin: 20px;
    display: flex;
    flex-direction: column;
    transition: all 0.4s;
    cursor: pointer;
}

.card:hover {
    transform: scale(1.1);
    transition: all 0.4s;
}

.card-title {
    width: 200px;
    position: relative;
    top: 65px;
    left: 65px;
}

.card-price {
    font-size: 23px;
    position: relative;
    top: -65px;
    left: 31px;
    color: red;
}

.card-color {
    width: 40px;
    font-size: 15px;
    position: relative;
    top: -282px;
    left: 98px;
}

.card-year {
    width: 40px;
    font-size: 20px;
    position: relative;
    top: -180px;
    left: 180px;
}

.card-img {
    box-shadow: 0px 2px 13px grey;
}

/*Search*/
.search {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}

.search-form {
    margin: auto;
}

.search-text {
    position: absolute;
    top: 335px;
    left: 20px;
    font-size: 20px;
    max-width: 300px;
    color: rgb(14, 89, 119);
}

.search-btn {
    width: 100px;
    font-size: 20px;
    margin-left: 20px;
    border-radius: 15px;
    border: 1px solid rgb(14, 89, 119);
    color: rgb(14, 89, 119);
    transition: all 0.4s;
    cursor: pointer;
}

.search-btn:hover {
    transform: scale(1.1);
    transition: all 0.4s;
}

.search-input {
    height: 23px;
    width: 250px;
    border: 1px solid rgb(14, 89, 119);
}
/*Not found page(404)*/
.not-found-page-title {
    font-size: 40px;
    text-align: center;
}

.link-not-found {
    font-size: 30px;
    margin-left: 100px;
}

.not-found-404 {
    margin: 0;
    font-size: 160px;
    text-align: center;
}